import React, { FC } from "react";
import styles from "./TableComponent.module.css";
import { Col, Row, message } from "antd";
import { Mise } from "../../models/Mise";
import { Action, ActionType } from "../../models/Action";
import { Paquet } from "../../models/Paquet";
import { Carte } from "../../models/Carte";
import JoueurComponent from "../JoueurComponent/JoueurComponent";
import GroupeCartesComponent from "../GroupeCartesComponent/GroupeCartesComponent";
import MainComponent from "../MainComponent/MainComponent";

interface TableComponentProps {
	mise: Mise;
	action: Action;
	paquet: Paquet;
	ouvert: boolean;
	auto: boolean;
	avecQuettee: boolean;
	nextAction: () => void;
	onCartesChange: (paquet: Paquet) => void;
}

export interface TableComponentMethods {
	onCliqueCarte: (carte: Carte | undefined) => void;
}

const TableComponent: React.ForwardRefRenderFunction<TableComponentMethods, TableComponentProps> = (props, ref) => {
	const [paquet, setPaquet] = React.useState<Paquet>(props.paquet);
	const [cartes1, setCartes1] = React.useState<Carte[]>(props.paquet.joueur1.cartes);
	const [cartes2, setCartes2] = React.useState<Carte[]>(props.paquet.joueur2.cartes);
	const [cartes3, setCartes3] = React.useState<Carte[]>(props.paquet.joueur3.cartes);
	const [cartes4, setCartes4] = React.useState<Carte[]>(props.paquet.joueur4.cartes);

	React.useImperativeHandle(ref, () => ({ onCliqueCarte }));

	function onCliqueCarte(carte: Carte | undefined) {
		if (!carte) return;
		if (paquet.attendre) {
			return;
		}
		paquet.cliqueCarte(carte, props.action.joueur, props.action, props.mise.atout);
		if (props.action.cptJoueur === 0) {
			paquet.setSorteDemandee(carte, props.mise.atout);
		}

		if (props.action.type === ActionType.DISCARTER && carte.isAtout(props.mise.atout)) {
			paquet.atoutsJoues++;
		}
		if (props.action.cptCarte === 1) {
			if (paquet.atoutsJoues > 0) {
				onDiscarterAtout();
			}
			paquet.atoutsJoues = 0;
		}
		props.nextAction();
	}

	function onDiscarterAtout() {
		const nCartes = paquet.atoutsJoues === 1 ? "1 carte" : "2 cartes";
		message.info(`${props.action.joueur.getNom()} a discarter ${nCartes} d'atout.`);
	}
	React.useEffect(() => {
		setPaquet(props.paquet);
	}, [props.paquet]);
	React.useEffect(() => {
		console.log(props.paquet.joueur1.cartes.length);
	}, [props.paquet.joueur1.cartes.length]);
	return (
		<div className={styles.TableComponent} data-testid="TableComponent">
			{/* Partenaire */}
			<div style={{ marginBottom: "60px" }}>
				<JoueurComponent
					mise={props.mise}
					paquet={paquet}
					joueurIdx={2}
					action={props.action}
					cliqueCarte={(carte: Carte) => onCliqueCarte(carte)}
					ouvert={props.ouvert}
					auto={props.auto}
					nextAction={props.nextAction}
					onCartesChange={props.onCartesChange}
				></JoueurComponent>
			</div>
			{/* Adversaires et Quettée */}
			<Row style={{ marginTop: "0px", marginBottom: "30px" }}>
				{/* Gauche */}
				<Col style={{ marginRight: "120px" }}>
					<JoueurComponent
						mise={props.mise}
						paquet={paquet}
						joueurIdx={1}
						action={props.action}
						cliqueCarte={(carte: Carte) => onCliqueCarte(carte)}
						ouvert={props.ouvert}
						auto={props.auto}
						nextAction={props.nextAction}
						onCartesChange={props.onCartesChange}
					></JoueurComponent>
				</Col>
				{/* Quettée */}
				{props.avecQuettee && props.action.type === ActionType.GAGER && (
					<Col style={{ marginTop: "52px" }}>
						<GroupeCartesComponent
							paquet={props.paquet}
							action={props.action}
							actif={false}
							ouvert={props.ouvert}
							auto={props.auto}
							cartes={paquet.getQuettee()}
							nextAction={props.nextAction}
							onCartesChange={props.onCartesChange}
						></GroupeCartesComponent>
					</Col>
				)}
				{/* Main */}
				{(props.action.type === ActionType.JOUER || props.action.type === ActionType.REMPORTER) && (
					<Col>
						<MainComponent paquet={paquet} ouvert={props.ouvert}></MainComponent>
					</Col>
				)}
				{/* Droite */}
				<Col style={{ marginLeft: "120px" }}>
					<JoueurComponent
						mise={props.mise}
						paquet={paquet}
						joueurIdx={3}
						action={props.action}
						cliqueCarte={(carte: Carte) => onCliqueCarte(carte)}
						ouvert={props.ouvert}
						auto={props.auto}
						nextAction={props.nextAction}
						onCartesChange={props.onCartesChange}
					></JoueurComponent>
				</Col>
			</Row>
			{/* Moi */}
			<div style={{ marginTop: "105px" }}>
				<JoueurComponent
					moi={true}
					mise={props.mise}
					paquet={paquet}
					joueurIdx={0}
					action={props.action}
					cliqueCarte={(carte: Carte) => onCliqueCarte(carte)}
					ouvert={true}
					auto={props.auto}
					nextAction={props.nextAction}
					onCartesChange={props.onCartesChange}
				></JoueurComponent>
			</div>
		</div>
	);
};

export default React.forwardRef(TableComponent);
