import React, { FC } from "react";
import styles from "./GroupeCartesComponent.module.css";
import { Col, message, Row } from "antd";
import { Carte, Sorte } from "../../models/Carte";
import { Mise } from "../../models/Mise";
import { Action, ActionType } from "../../models/Action";
import CarteComponent from "../CarteComponent/CarteComponent";
import { Paquet } from "../../models/Paquet";
import { outputError } from "../../utils/generalUtil";

interface GroupeCartesComponentProps {
	actif: boolean;
	mise?: Mise;
	action: Action;
	paquet: Paquet;
	cartes?: Carte[];
	ouvert: boolean;
	auto: boolean;
	moi?: boolean;
	joueurIdx?: number;
	cliqueCarte?: (carte: Carte) => void;
	nextAction: () => void;
	onCartesChange: (paquet: Paquet) => void;
}

const GroupeCartesComponent: FC<GroupeCartesComponentProps> = (props: GroupeCartesComponentProps) => {
	const [cartes, setCartes] = React.useState<Carte[]>(getCartes());
	const [paquet, setPaquet] = React.useState<Paquet>(props.paquet);

	function getCartes(): Carte[] {
		if (props.cartes) return props.cartes;
		if (props.joueurIdx !== undefined) return paquet.joueurs[props.joueurIdx].cartes;
		console.log("Pas de carte!");
		return [];
	}
	function onClick(e: any, carte: Carte) {
		if (isDisabled(carte) || !isClickable()) {
			return;
		}
		if (e.detail === 2 && props.actif) {
			onCliqueCarte(carte);
		}
	}
	function onCliqueCarte(carte: Carte) {
		if (paquet.attendre) {
			return;
		}

		if (!props.mise) return outputError("Pas de mise!");
		if (props.joueurIdx === undefined) return outputError("Pas de joueur!");
		paquet.cliqueCarte(carte, props.action.joueur, props.action, props.mise.atout);
		if (props.action.cptJoueur === 0) {
			paquet.setSorteDemandee(carte, props.mise.atout);
		}

		if (props.action.type === ActionType.DISCARTER && carte.isAtout(props.mise.atout)) {
			paquet.atoutsJoues++;
		}
		if (props.action.cptCarte === 1) {
			if (paquet.atoutsJoues > 0) {
				onDiscarterAtout();
			}
			paquet.atoutsJoues = 0;
		}
		props.onCartesChange(paquet);
		const newCartes = [];
		for (let carte of paquet.joueurs[props.joueurIdx].cartes) {
			newCartes.push(carte);
		}
		setCartes(newCartes);
		props.nextAction();
	}

	function onDiscarterAtout() {
		const nCartes = paquet.atoutsJoues === 1 ? "1 carte" : "2 cartes";
		message.info(`${props.action.joueur.getNom()} a discarter ${nCartes} d'atout.`);
	}

	function isDisabled(carte: Carte): boolean {
		if (!props.actif) {
			return false;
		}
		if (props.action === null) {
			return false;
		}
		if (props.mise === null) {
			return false;
		}
		if (props.action.type === ActionType.DISCARTER && carte.points !== 0) {
			return true;
		}
		if (props.action.type === ActionType.JOUER) {
			if (!paquet.sorteDemandee || !props.mise) return false;
			return carte.isDisabled(cartes, paquet.sorteDemandee, props.mise.atout);
		}
		return false;
	}

	function isClickable() {
		return props.actif && props.action.type !== ActionType.CHOISIR_ATOUT;
	}

	React.useEffect(() => {
		setCartes(getCartes());
	}, [paquet, props.cartes]);

	return (
		<div className={styles.GroupeCartesComponent} data-testid="GroupeCartesComponent">
			<Row gutter={6}>
				{/* Chaque carte */}
				{cartes.map((carte: Carte, index: number) => (
					<Col
						onClick={(e) => onClick(e, carte)}
						style={{ marginTop: carte.surelevee && props.actif ? "-10px" : "0px" }}
						key={index}
					>
						<CarteComponent
							moi={props.moi}
							clickable={isClickable()}
							disabled={isDisabled(carte)}
							carte={carte}
							ouvert={props.ouvert}
							auto={props.auto}
						></CarteComponent>
					</Col>
				))}
			</Row>
		</div>
	);
};

export default GroupeCartesComponent;
