import React, { FC } from "react";
import { Action } from "../../models/Action";
import { Carte, Sorte } from "../../models/Carte";
import { Joueur } from "../../models/Joueur";
import { Mise } from "../../models/Mise";
import { Paquet } from "../../models/Paquet";
import GroupeCartesComponent from "../GroupeCartesComponent/GroupeCartesComponent";
import styles from "./JoueurComponent.module.css";

interface JoueurComponentProps {
	paquet: Paquet;
	joueurIdx: number;
	moi?: boolean;
	mise: Mise;
	action: Action;
	auto: boolean;
	ouvert: boolean;
	cliqueCarte: (carte: Carte) => void;
	nextAction: () => void;
	onCartesChange: (paquet: Paquet) => void;
}

const JoueurComponent: FC<JoueurComponentProps> = (props: JoueurComponentProps) => {
	const [joueur, setJoueur] = React.useState<Joueur>(props.paquet.joueurs[props.joueurIdx]);

	function onDiscarte(carte: Carte) {
		props.cliqueCarte(carte);
	}

	React.useEffect(() => {
		setJoueur(props.paquet.joueurs[props.joueurIdx]);
	}, [props.paquet]);
	return (
		<div className={`${styles.JoueurComponent} App-center`} data-testid="JoueurComponent">
			{/* Nom au-dessus */}
			{!props.moi && (
				<p
					style={{
						fontSize: "24px",
						marginBottom: "0px",
						color: joueur.actif ? "rgb(32,166,237)" : "white",
					}}
				>
					{joueur.getNom()}
				</p>
			)}
			{/* Cartes */}
			<GroupeCartesComponent
				paquet={props.paquet}
				joueurIdx={props.joueurIdx}
				moi={props.moi}
				mise={props.mise}
				action={props.action}
				actif={joueur.actif}
				cliqueCarte={onDiscarte}
				ouvert={props.ouvert}
				auto={props.auto}
				cartes={joueur.cartes}
				nextAction={props.nextAction}
				onCartesChange={props.onCartesChange}
			></GroupeCartesComponent>
			{/* Nom en-dessous */}
			{props.moi && (
				<p
					style={{
						fontSize: "24px",
						marginBottom: "0px",
						color: joueur.actif ? "rgb(32,166,237)" : "white",
					}}
				>
					{joueur.nom}
				</p>
			)}
		</div>
	);
};

export default JoueurComponent;
